:root {
  --ins-color--orange: #ec7a08;
}

:root {
  --ins-c-table-toolbar--PaddingVertical: 16px;
  --ins-c-table-toolbar--PaddingHorizontal: 32px;
}

.ins-c-table__toolbar, .ins-c-table__toolbar-results {
  background: var(--pf-global--Color--light-100);
  border-bottom: 1px solid var(--pf-global--BorderColor--light-200);
  padding: var(--ins-c-table-toolbar--PaddingVertical) var(--ins-c-table-toolbar--PaddingHorizontal);
}

.ins-c-table__toolbar.ins-m-footer {
  border-top: 1px solid var(--pf-global--BorderColor--light-200);
}

.ins-c-table__toolbar-results {
  color: var(--pf-global--Color--200);
}

.ins-c-table__toolbar-results-count + .ins-c-table__toolbar-results-selected {
  margin-left: 15px;
  margin-left: 0.9375rem;
  padding-left: 15px;
  padding-left: 0.9375rem;
  border-left: 2px solid var(--pf-global--BorderColor--light);
}

@media only screen and (max-width: 768px) {
  .ins-c-table__toolbar {
    padding: var(--pf-global--spacer--md) var(--pf-global--spacer--md);
  }

  .ins-c-table-empty-state.pf-m-grid-md.pf-c-table [data-label] {
    --pf-c-table--cell--hidden-visible--Display: 0;
  }
  .ins-c-table-empty-state.pf-m-grid-md.pf-c-table tr:first-of-type > td::before {
    display: none;
  }
}